import { Component } from '@angular/core';

@Component({
    selector: 'backup',
    templateUrl: './backup.component.html'
})
export class BackupComponent {

    botaoRealiza: boolean;

    messageSucesso: boolean;
    messageErroExecucao: boolean;
    messageErroNaoPossivel: boolean;

    constructor(){
        
    }

    realizarBackup() {
        this.messageSucesso = false;

        //disativa o botao
        this.botaoRealiza = true;
        console.log('Realizar backup');
        this.setSucessoBackup();
    }

    setSucessoBackup(){
        this.messageSucesso = true;

        setTimeout(() => {
            this.messageSucesso = false;
            this.botaoRealiza = false;
        }, 10000);
    }

    voltar() {
        console.log('voltar botao');
    }

   
}


