import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import {
  MatIconModule, MatToolbar, MatToolbarModule, MatButtonModule, 
  MatTableModule, MatPaginatorModule, MatFormFieldModule, MatInputModule, MatMenuModule, MatSelectModule,
  MatCardModule
} from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { RouterModule } from "@angular/router";

import { Services } from "../services";
import { routing } from "./app.routing";
import { AppComponent } from './app.component';
import { LinksComponent } from './links.component';
import { HomeComponent } from './home.component';
import { NotificacoesComponent } from './notificacoes.component';
import { UsuariosComponent } from './usuarios.component';
import { BackupComponent } from './backup.component';
import { LoginComponent } from './login.component';
import { MenuComponent } from './menu.component';

import { LoginGuard } from './login.guard';

import { ConfigurationModule } from '@stefanini/configuration';

export const APP_CONFIG = {
  'my-config': 'My Config',
  'my-config-env': {
    'dev': 'Config Dev',
    'prod': 'Config Dev'
  }
};

// import url-resolver module
import { UrlResolverModule } from '@stefanini/url-resolver';

export const APP_ROUTE = {
  '_defaults': {
    'host': 'http://api.example.com/'
  },
  'user': {
    'url': 'users/{id}',
    'method': 'GET',
    'headers': {
      'content-type': 'application/json'
    },
    'params': {
      'id': {
        'type': 'number',
        'required': true
      }
    }
  },
  'github': {
    'url': 'https://api.github.com/users/seeschweiler',
    'method': 'GET',
  },
  'resolver': {
    'url': 'http://localhost:8080/resolver',
    'method': 'POST',
  }
};

// Import http module
 import { HttpModule, DefaultPlugins } from '@stefanini/http';

@NgModule({
  declarations: [
    AppComponent, LinksComponent, MenuComponent, HomeComponent, NotificacoesComponent, UsuariosComponent, BackupComponent, LoginComponent
  ],
  imports: [
    RouterModule,
    routing,
    BrowserModule,
    CommonModule,
    MatIconModule,
    MatButtonModule,
    MatToolbarModule,
    MatTableModule,
    MatPaginatorModule,
    MatFormFieldModule,
    MatInputModule,
    MatMenuModule,
    MatSelectModule,
    MatCardModule,
    FormsModule,
    ConfigurationModule.initialize(APP_CONFIG, 'dev'),
    UrlResolverModule.initialize(APP_ROUTE),
    HttpModule.initialize(DefaultPlugins)
  ],
  exports: [
    MatIconModule,
    MatToolbarModule,
    MatButtonModule,
    MatTableModule,
    MatPaginatorModule,
    MatFormFieldModule,
    BrowserAnimationsModule
  ],
  providers: [
    Services,
    {provide: LocationStrategy, useClass: HashLocationStrategy}, 
    LoginGuard,
   // Services
    ],
  bootstrap: [AppComponent]
})
export class AppModule { }


